@import '~normalize.css/normalize.css';

@titleBarHeight: 40px;
@menuBarHeight: 56px;
@borderColor: #eee;

body {
  background: #F9F9F9;
  line-height: 1.5;
  font-size: 14px;
}

a {
  text-decoration: none;
  color: #9D9D9D;
}

.icon {
  font-size: 20px;

  &.orange {
    color: #FFB44E;
  }
  &.blue {
    color: #1EB1E8;
  }
  &.red {
    color: #FC6166;
  }
  &.green {
    color: #2CBD98;
  }
}

#title-bar {
  position: fixed;
  z-index: 100;
  background: #2C9BF9;
  color: #fff;
  text-align: center;
  height: @titleBarHeight;
  line-height: @titleBarHeight;
  width: 100%;
  font-size: 18px;

  .list {
    position: absolute;
    right: 10px;
    top: 0;
    .icon {
      color: #fff;
    }
  }
}

#container {
  color: #8D8D8D;
  padding: @titleBarHeight 0 @menuBarHeight 0;

  .datetime {
    text-align: center;
    padding: 10px 0;
  }

  .realtime-info {
    background: #fff;
    padding: 10px 20px 0;
    position: relative;
    margin-bottom: 20px;

    h3 {
      border-bottom: 1px solid #F3F3F3;
      margin: 0;
      font-weight: 300;
      color: #2A2A2A;
      padding-bottom: 10px;
    }
    .more {
      position: absolute;
      right: 10px;
      top: 8px;

      .icon {
        font-size: 16px;
      }
    }
    strong {
      color: #2A2A2A;
    }
    .num {
      padding: 20px 20px 15px;
      border-bottom: 1px solid #F3F3F3;
      display: flex;

      div {
        flex: 1;
        text-align: center;
        .green-num {
          color: #0BD36C;
          font-size: 36px;
        }
        .orange-num {
          color: #FFBF3A;
          font-size: 26px;
        }
        .type {
          color: #333;
        }
      }

    }
    .details {
      display: flex;
      padding: 10px 0;

      div {
        flex: 1;
        text-align: center;

        &:first-child {
          border-right: 1px solid #F2F2F2;
        }

      }
    }
  }


  &.menu {
    display: flex;
    width: 100%;
    flex-flow: row wrap;

    .menu-item {
      display: block;
      flex: 0 0 33.33%;
      height: 130px;
      background: white;
      border-right: 1px solid @borderColor;
      border-bottom: 1px solid @borderColor;
      text-align: center;
      box-sizing: border-box;
      padding-top: 26px;

      .icon {
        display: block;
        font-size: 24px;
      }

      &:hover, &.select {
        background: @borderColor;
      }
    }
  }

  &.rate {
    .item-wrapper {
      display: flex;
      width: 100%;
      flex-flow: row wrap;
      background: white;

      .item {
        flex: 0 0 50%;
        border-right: 1px solid @borderColor;
        border-bottom: 1px solid @borderColor;
        box-sizing: border-box;
        padding-top: 30px;
        padding-left: 15px;
        height: 100px;

        * {
          vertical-align: middle;
        }
        .icon {
          font-size: 24px;
        }
      }
    }
    .title {
      color: #A1A1A1;
      height: 40px;
      line-height: 40px;
      padding-left: 20px;
    }
  }
}

#menu-bar {
  display: flex;
  box-sizing: border-box;
  position: fixed;
  width: 100%;
  bottom: 0;
  z-index: 100;
  height: @menuBarHeight;
  font-size: 14px;
  box-shadow: 0 0 5px 0 rgba(150, 150, 150, 0.5);
  background: #fff;

  a {
    flex: 1;
    display: block;
    text-align: center;
    color: #D1D1D1;

    .icon {
      color: #D1D1D1;
      display: block;
    }

    &.current {
      color: #242424;

      .icon {
        color: #2B9AF6;
      }
    }

    &.center {
      flex: 0 0 60px;
      position: relative;

      .icon-wrapper {
        display: block;
        position: absolute;
        left: 0;
        width: 50px;
        height: 50px;
        top: -20px;
        border-radius: 50%;
        background: #2B9AF6;

        .icon {
          color: #fff;
          padding-top: 10px;
        }
      }

      .menu {
        position: absolute;
        bottom: 4px;
        left: 12px;
      }
    }
  }
}


